/* roboto-regular - latin */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/roboto-v20-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Roboto'), local('Roboto-Regular'),
       url('../fonts/roboto-v20-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/roboto-v20-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/roboto-v20-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('../fonts/roboto-v20-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/roboto-v20-latin-regular.svg#Roboto') format('svg'); /* Legacy iOS */
}
/* roboto-500 - latin */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  src: url('../fonts/roboto-v20-latin-500.eot'); /* IE9 Compat Modes */
  src: local('Roboto Medium'), local('Roboto-Medium'),
       url('../fonts/roboto-v20-latin-500.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/roboto-v20-latin-500.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/roboto-v20-latin-500.woff') format('woff'), /* Modern Browsers */
       url('../fonts/roboto-v20-latin-500.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/roboto-v20-latin-500.svg#Roboto') format('svg'); /* Legacy iOS */
}

html {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: normal;
  box-sizing: border-box;
  height: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  height: 100%;
  background: #333333;
  overscroll-behavior: none;
}

.scroll-hidden {
  overflow: hidden;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.blur {
  transition: filter 1s;
  filter: blur(30px);
}

.blurOut {
  transition: animation .6s;
  animation: blurOut .6s;
  -webkit-animation: blurOut .6s;
}

@keyframes blurOut {
  0% {
    filter: blur(30px);
  }

  100% {
    filter: blur(0px);
  }
}

@-webkit-keyframes blurOut {
  0% {
    filter: blur(30px);
  }

  100% {
    filter: blur(0px);
  }
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 auto;
  background-image: url(../img/header-background.svg);
  height: 35vh;
  min-height: 240px;
  background-size: cover;
}

.cardContainer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  flex: 1 0 auto;
  padding: 0 40px;
}

.noCharactersFound {
  text-align: center;
  font-size: 18px;
  color: #FFFFFF;
  margin-top: 100px;
}

.logoWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 330px;
  height: 180px;
}

.logoStar {
  height: 80px;
  background-image: url(../img/star.svg);
  background-size: cover;
}

.logoWars {
  height: 80px;
  background-image: url(../img/wars.svg);
  background-size: cover;
}

.logoText {
  font-weight: bold;
  text-align: center;
  letter-spacing: 0.11em;
  font-size: 18px;
  line-height: 20px;
  color: #FFFFFF;
}

.form {
  position: relative;
  margin: 50px 0px 30px 0px;
}

.input {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  width: 600px;
  height: 40px;
  border-width: 0 0 1px;
  border-color: #808080;
  border-style: none;
  border-bottom-style: solid;
  background: none;
  outline: none;
  font-size: 18px;
  padding: 0;
}

.input:enabled {
  color: white;
}

.searchButton {
  position: absolute;
  right: 0px;
  bottom: 4px;
  background-image: url(../img/ic_search_24px.svg);
  width: 32px;
  height: 32px;
  background-color: transparent;
  border: none;
  outline: none;
  padding: 0;
}

.searchButton:hover {
  cursor: pointer;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 auto;
  background: #1A1A1A;
  height: 120px;
  margin-top: 100px;
}

.footerText {
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.11em;
  color: #FFFFFF;
}

ul.cardList > div {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

.card {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 570px;
  height: 320px;
  background: #1A1A1A;
  border-radius: 8px;
  box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.6);
  margin: 20px auto;
  font-weight: bold;
  font-size: 18px;
  color: #FFFFFF;
}

.card:hover {
  box-shadow: 0px 5px 40px rgba(37, 137, 167, 0.897);
  cursor: pointer;
}

.avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 48px;
  line-height: 56px;
  width: 80px;
  height: 80px;
  background: #01A39D;
  border-radius: 50%;
}

.avatarWrapper {
  display: flex;
  justify-content: center;
  flex-flow: column wrap;
  align-items: center;
}

.avatarName {
  text-align: center;
  line-height: 21px;
  font-size: 18px;
  padding: 10px 0px 9px 0px;
}

.avatarGender {
  text-align: center;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 15px;
  color: #808080;
}

.fadeInUp {
  animation: fadeInUp 1s;
  -webkit-animation: fadeInUp 1s;
  -moz-animation: fadeInUp 1s;
}

@keyframes fadeInUp {
  from {
    transform: translateY(40px);
    opacity: 0;
  }

  to {
    transform: translateY(0px);
    opacity: 1;
  }
}

@-webkit-keyframes fadeInUp {
  from {
    transform: translateY(40px);
    opacity: 0;
  }

  to {
    transform: translateY(0px);
    opacity: 1;
  }
}

@media screen and (max-width: 767px) {
  html {
    touch-action: manipulation;
  }
  
  .header {
    width: 100%;
    height: 240px;
  }

  .logoWrapper {
    width: 212px;
    height: 125px;
  }

  .logoStar {
    height: 50px;
  }

  .logoWars {
    height: 50px;
  }

  .logoText {
    font-size: 9px;
    line-height: 9px;
    margin-top: 2px;
  }

  .form {
    width: 272px;
  }

  .input {
    width: 272px;
    height: 40px;
    font-size: 16px;
    line-height: 19px;
    -webkit-appearance: none;
    -moz-appearance: none;
  }

  .searchButton {
    bottom: 7.5px;
    width: 25px;
    height: 25px;
    background-size: cover;
    -webkit-appearance: none;
    -moz-appearance: none;
  }

  .cardContainer {
    padding: 0 20px;
  }

  .card {
    width: 272px;
    height: 200px;
    margin: 20px auto;
  }

  .footer {
    height: 104px;
    margin-top: 100px;
  }

  .footerText {
    width: 199px;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
  }
}